@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap");
/* reset styles */
* {
  color: inherit;
  margin: 0;
}

html {
  height: 100%;
  overflow-y: scroll;
}

body {
  font-family: Rubik;
  background-color: rgb(244, 243, 243);
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}

ul {
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

hr {
  border: 0;
  border-top: 1px dotted #efefef;
}

img {
  max-width: 100%;
}

/**/
.textarea-comment {
  font-family: Rubik;
  width: 70%;
  resize: none;
  outline: none;
  border-radius: 8px;
  border: 1px solid lightgray;
  padding: 8px 10px 8px 10px;
  height: fit-content;
}
.textarea-comment:focus {
  border: 1px solid hsl(238, 40%, 52%);
}
.textarea-comment::placeholder {
  color: hsl(211, 10%, 45%);
}

section {
  display: flex;
  flex-direction: column;
  justify-self: center;
  justify-content: center;
  align-items: center;
  gap: 20px;
  height: 100%;
  width: 80%;
  padding: 50px 0;
}
section .comments__container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
section .principal-comment-container {
  width: 95%;
}
.reply-comment-container {
  width: 100%;
}
section .principal-comment-container .comment-container-content,
section .reply-comment-container .comment-container-content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
}
section
  .principal-comment-container
  .comment-container-content
  .right-comment-content,
section
  .reply-comment-container
  .comment-container-content
  .right-comment-content {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-direction: column;
  width: 100%;
}
section
  .principal-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos,
section
  .reply-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
section
  .principal-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .profile-container,
section
  .reply-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 14px;
}
section
  .principal-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .profile-container
  .avatar-name,
section
  .reply-comment-container
  .comment-container-content
