}
section
  .principal-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .profile-container
  .date,
section
  .reply-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .profile-container
  .date {
  font-weight: 400;
  color: hsl(211, 10%, 45%);
}
section
  .principal-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .reply-container,
section
  .reply-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .reply-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
section
  .principal-comment-container
  .comment-container-content
  .right-comment-content
  .comment-infos
  .reply-container:hover,
section
  .reply-comment-container
  .comment-container-content
